import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';

import { MessagerModule } from '@farris/ui-messager';
import { FarrisDialogModule } from '@farris/ui-dialog';
import { PerfectScrollbarModule } from '@farris/ui-perfect-scrollbar';
import { LookupModule } from '@farris/ui-lookup';
import { LocaleModule } from '@farris/ui-locale';
import { FarrisCommonModule } from '@farris/ui-common';
import { ComboLookupModule } from '@farris/ui-combo-lookup';
import { FarrisFormsModule } from '@farris/ui-forms';

import { SorterComponent } from './sort.component';
import { SortEditorComponent } from './sort-editor.component';
@NgModule({
    imports: [
        CommonModule,
        FormsModule,
        MessagerModule.forRoot(),
        PerfectScrollbarModule,
        FarrisDialogModule,
        LookupModule,
        FarrisCommonModule,
        ComboLookupModule,
        LocaleModule,
        FarrisFormsModule
    ],
    declarations: [
        SortEditorComponent,
        SorterComponent
    ],
    exports: [
        SorterComponent,
        SortEditorComponent
    ],
    entryComponents: [
        SortEditorComponent
    ]
})
export class SorterModule {}
